let BaCayRequest = {};
BaCayRequest.REQUEST_NAME = {
    SIT_ON_REQUEST : "bc1",
    SIT_OUT_REQUEST : "bc2",
    STAND_UP_REQUEST : "bc3",
    BET_REQUEST : "bc6",
    BET_GA_REQUEST : "bc11",
    REGISTER_OWNER_REQUEST : "bc12",
    SELL_OWNER_REQUEST: "bc13"
}
//
BaCayRequest._BaseRequest = SmartFoxSDK.Class.extend({
    ctor(name){
        this._requestName = name;
        this._params = new SmartFoxSDK.SObject();
        this._roomId = undefined;
    },
    setRoomId(id){
        this._roomId = id;
        return this;
    },
    getRequestName(){
        return this._requestName;
    },
    toSRequest(){
        return new SmartFoxSDK.SmartFox.Requests.System.ExtensionRequest(this._requestName, this._params, this._roomId);
    }
});
//
BaCayRequest.SitOnRequest = BaCayRequest._BaseRequest.extend({
    ctor(){
        this._super(BaCayRequest.REQUEST_NAME.SIT_ON_REQUEST);
    },
    setDeskId(id){
        this._params.putInt(this.constructor.DESK_ID, id);
        return this;
    },
    setAutoSitOn(){
        this._params.putInt(this.constructor.AUTO_SIT_ON, -10);
        return this;
    }
});
BaCayRequest.SitOnRequest.DESK_ID = "id";
BaCayRequest.SitOnRequest.AUTO_SIT_ON = "as";
//
BaCayRequest.SitOutRequest = BaCayRequest._BaseRequest.extend({
    ctor(){
        this._super(BaCayRequest.REQUEST_NAME.SIT_OUT_REQUEST);
    },
    setSitOut(sit_out){
        this._params.putBool(this.constructor.IS_SIT_OUT, sit_out);
        return this;
    }
});
BaCayRequest.SitOutRequest.IS_SIT_OUT = "iso";
//
BaCayRequest.StandUpRequest = BaCayRequest._BaseRequest.extend({
    ctor(){
        this._super(BaCayRequest.REQUEST_NAME.STAND_UP_REQUEST);
    }
});
//
BaCayRequest.BetRequest = BaCayRequest._BaseRequest.extend({
    ctor(){
        this._super(BaCayRequest.REQUEST_NAME.BET_REQUEST);
    },
    setBet(bet){
        this._params.putDouble("c",bet);
        return this;
    }
});
BaCayRequest.BetGaRequest = BaCayRequest._BaseRequest.extend({
    ctor() {
        this._super(BaCayRequest.REQUEST_NAME.BET_GA_REQUEST);
    }
});
BaCayRequest.RegisterOwnerRequest = BaCayRequest._BaseRequest.extend({
    ctor(){
        this._super(BaCayRequest.REQUEST_NAME.REGISTER_OWNER_REQUEST);
    }
});
BaCayRequest.SellOwnerRequest = BaCayRequest._BaseRequest.extend({
    ctor(){
        this._super(BaCayRequest.REQUEST_NAME.SELL_OWNER_REQUEST);
    }
});
window.BaCayRequest = BaCayRequest;